"use client";

import React, { useState, useEffect, useRef, useCallback } from "react";
import { ChevronLeft, ChevronRight } from "lucide-react";
import useHorizontalScroll from "@/app/hooks/useHorizontalScroll";
import LiteYouTubeEmbed from "react-lite-youtube-embed";
import "react-lite-youtube-embed/dist/LiteYouTubeEmbed.css";

interface Review {
  id: number;
  src: string;
}

interface ReviewsProps {
  reviewsData: Review[];
}

function Testimonials({ reviewsData }: ReviewsProps) {
  const { scroll, scrollRef } = useHorizontalScroll();

  const [currentIndex, setCurrentIndex] = useState<number>(0);
  const [touchStart, setTouchStart] = useState<number>(0);
  const [touchEnd, setTouchEnd] = useState<number>(0);
  const [isDragging, setIsDragging] = useState<boolean>(false);
  const [dragOffset, setDragOffset] = useState<number>(0);
  const [isVideoPlaying, setIsVideoPlaying] = useState<boolean>(false);
  const autoPlayRef = useRef<NodeJS.Timeout | null>(null);

  const reviews = reviewsData;

  // Single ref for scroll animation
  const containerRef = useRef<HTMLDivElement>(null);
  const [isVisible, setIsVisible] = useState<boolean>(false);

  // Minimum swipe distance (in px) to trigger slide change
  const minSwipeDistance = 50;

  // Auto-play functionality
  const stopAutoPlay = useCallback(() => {
    if (autoPlayRef.current) {
      clearInterval(autoPlayRef.current);
    }
  }, []);

  const startAutoPlay = useCallback(() => {
    autoPlayRef.current = setInterval(() => {
      setCurrentIndex((prev) => (prev + 1) % reviews.length);
    }, 3000);
  }, [reviews.length]);

  useEffect(() => {
    if (!isVideoPlaying) {
      startAutoPlay();
    }
    return () => stopAutoPlay();
  }, [isVideoPlaying, startAutoPlay, stopAutoPlay]);

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            setIsVisible(true);
          }
        });
      },
      {
        threshold: 0.1,
        rootMargin: "0px",
      },
    );

    const currentRef = containerRef.current;
    if (currentRef) {
      observer.observe(currentRef);
    }

    return () => {
      if (currentRef) {
        observer.unobserve(currentRef);
      }
    };
  }, []);

  const ReviewCard = ({ review }: { review: { id: number; src: string } }) => {
    // Extract YouTube video ID from URL
    const getVideoId = (url: string) => {
      const videoIdMatch = url.match(
        /(?:youtu\.be\/|youtube\.com\/(?:embed\/|v\/|watch\?v=|watch\?.+&v=))([^?&"'>]+)/,
      );
      return videoIdMatch ? videoIdMatch[1] : "";
    };

    const handleVideoClick = () => {
      setIsVideoPlaying(true);
      stopAutoPlay();
    };

    return (
      <div
        className=" rounded-2xl w-full aspect-video relative overflow-hidden"
        onClick={handleVideoClick}
      >
        <LiteYouTubeEmbed
          id={getVideoId(review.src)}
          title="YouTube video"
          poster="maxresdefault"
          webp={true}
          activatedClass="lty-activated"
          lazyLoad={true}
        />
      </div>
    );
  };

  const onTouchStart = (e: React.TouchEvent<HTMLDivElement>) => {
    stopAutoPlay();
    setTouchEnd(0);
    setTouchStart(e.targetTouches[0].clientX);
    setIsDragging(true);
  };

  const onTouchMove = (e: React.TouchEvent<HTMLDivElement>) => {
    const currentTouch = e.targetTouches[0].clientX;
    setTouchEnd(currentTouch);

    // Calculate drag offset for visual feedback
    const offset = currentTouch - touchStart;
    setDragOffset(offset);
  };

  const onTouchEnd = () => {
    setIsDragging(false);
    setDragOffset(0);

    if (!touchStart || !touchEnd) {
      startAutoPlay();
      return;
    }

    const distance = touchStart - touchEnd;
    const isLeftSwipe = distance > minSwipeDistance;
    const isRightSwipe = distance < -minSwipeDistance;

    if (isLeftSwipe) {
      setCurrentIndex((prev) => (prev + 1) % reviews.length);
    }

    if (isRightSwipe) {
      setCurrentIndex((prev) => (prev - 1 + reviews.length) % reviews.length);
    }

    // Restart auto-play after swipe
    startAutoPlay();
  };

  const prevSlide = () => {
    stopAutoPlay();
    setCurrentIndex((prev) => (prev - 1 + reviews.length) % reviews.length);
    startAutoPlay();
  };

  const nextSlide = () => {
    stopAutoPlay();
    setCurrentIndex((prev) => (prev + 1) % reviews.length);
    startAutoPlay();
  };

  return (
    <div
      ref={containerRef}
      className="text-white w-full mx-auto md:m-0 md:contain-none px-6 pb-12 md:p-0 md:px-16 md:pr-0 bg-primary"
    >
      {/* Heading with fade-in from left animation */}
      <div
        className={`transition-all duration-700 ease-out ${
          isVisible ? "opacity-100 translate-y-0" : "opacity-0 -translate-y-12"
        }`}
      >
        <h6 className="text-2xl">What</h6>
        <h6 className="text-xl md:text-2xl pb-1">
          <span className="font-bold text-4xl sm:text-3xl italic">
            Living Uplife
          </span>{" "}
          <span className="block text-right sm:text-left sm:inline text-2xl sm:text-inherit">
            Looks Like
          </span>
        </h6>
      </div>

      {/* Mobile Carousel with Swipe - visible below md breakpoint */}
      <div
        className={`md:hidden mt-12 relative transition-all duration-700 ease-out ${
          isVisible ? "opacity-100 translate-x-0" : "opacity-0 translate-x-12"
        }`}
      >
        <div className="relative overflow-hidden">
          <div
            className={`flex ${
              isDragging ? "" : "transition-transform duration-500 ease-in-out"
            }`}
            style={{
              transform: `translateX(calc(-${
                currentIndex * 100
              }% + ${dragOffset}px))`,
            }}
            onTouchStart={onTouchStart}
            onTouchMove={onTouchMove}
            onTouchEnd={onTouchEnd}
          >
            {reviews.map((review) => (
              <div key={review.id} className="flex-shrink-0 w-full">
                <ReviewCard review={review} />
              </div>
            ))}
          </div>

          {/* Navigation Arrows */}
          <button
            onClick={prevSlide}
            className="absolute left-0 top-1/2 -translate-y-1/2 bg-white/30 p-2 rounded-full shadow-lg hover:bg-white transition-colors z-10"
            aria-label="Previous review"
          >
            <ChevronLeft className="w-6 h-6 text-black" />
          </button>
          <button
            onClick={nextSlide}
            className="absolute right-0 top-1/2 -translate-y-1/2 bg-white/30 p-2 rounded-full shadow-lg hover:bg-white transition-colors z-10"
            aria-label="Next review"
          >
            <ChevronRight className="w-6 h-6 text-black" />
          </button>
        </div>

        {/* Carousel Indicators */}
        <div className="flex justify-center gap-2 mt-12">
          {reviews.map((review, index) => (
            <button
              key={review.id}
              onClick={() => {
                stopAutoPlay();
                setCurrentIndex(index);
                startAutoPlay();
              }}
              className={`w-2 h-2 rounded-full transition-all ${
                currentIndex === index ? "bg-white w-6" : "bg-white/40"
              }`}
              aria-label={`Go to slide ${index + 1}`}
            />
          ))}
        </div>
      </div>

      {/* cards  - visible at md breakpoint and above */}
      <div className={`hidden relative md:block w-full `}>
        <div
          className={`hidden md:grid grid-rows-1 grid-flow-col auto-cols-[30%] gap-5 mt-12 md:pr-16
         overflow-x-auto justify-between overflow-y-visible scroll-smooth scrollbar-hide
         transition-all duration-700 ease-out  ${
           isVisible ? "opacity-100 translate-y-0" : "opacity-0 translate-y-12"
         }`}
          ref={scrollRef}
        >
          {reviews.map((review) => (
            <ReviewCard key={review.id} review={review} />
          ))}
        </div>

        <button
          onClick={() => scroll("left")}
          className="absolute left-0 top-1/2 -translate-y-1/2 bg-white/30 backdrop-blur-sm p-2 rounded-full shadow-lg hover:bg-white transition-colors z-10"
          aria-label="Previous review"
        >
          <ChevronLeft className="w-6 h-6 text-black" />
        </button>

        {/* no right padding that's why right 16 intead of 0 */}
        <button
          onClick={() => scroll("right")}
          className="absolute right-16 top-1/2 -translate-y-1/2 bg-white/30 backdrop-blur-sm p-2 rounded-full shadow-lg hover:bg-white transition-colors z-10"
          aria-label="Next review"
        >
          <ChevronRight className="w-6 h-6 text-black" />
        </button>
      </div>
    </div>
  );
}

export default Testimonials;
